import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const Ads = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;

  height: 200px;
  max-width: 430px;
`;

const AdsTitle = styled.h4`
  font-weight: 300;
  font-size: 32px;
  line-height: 130%;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 20px;
`;

const AdsDesc = styled(Text)`
  color: #3c3c3c;
`;

const AdsImg = styled.img`
  margin-bottom: 25px;
`;

const Line = styled.div`
  width: 1px;
  height: 243px;

  @media (max-width: 1200px) {
    height: 300px;
  }

  background: #e7f5ff;
`;

const AdsType = ({ adImg, adTitle, description, line }) => {
  return (
   <>
   <Ads>
      <AdsImg loading="lazy" src={adImg} />
      <AdsTitle>{adTitle}</AdsTitle>
      <AdsDesc>{description}</AdsDesc>
    </Ads>
    {line && <Line />}
   </>
  );
};

export default AdsType;
