import styled from "styled-components";
import { Text, Title2 } from "../../assets/styles/component";
import Star_1 from "../../assets/images/Star_1.svg";
import Star_2 from "../../assets/images/Star_2.svg";
import textAds from "../../assets/images/AdsText.svg";
import nativeAds from "../../assets/images/AdsNative.svg";
import videoAds from "../../assets/images/AdsVideo.svg";
import AdsType from "./adstypeDesktop";
import { EngLoc } from "../../assets/constants/localizations";

const StyledAbout = styled.section`
  padding: 80px 0 150px 0;
  gap: 120px;
  background-color: #f4f8fc;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const AboutPart = styled.div`
  margin: 0 15%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;

  @media (max-width: 1600px) {
    &:first-child {
      flex-wrap: wrap;
      justify-content: center;
      gap: 60px;
    }

    width: calc(100% - 10%);
    margin: auto;
  }
`;

const HightTitle = styled(Title2)`
  font-weight: 300;
  font-size: 146px;
  line-height: 110%;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
`;

const AboutText = styled(Text)`
  width: 100%;
  max-width: 619px;
  height: fit-content;

  color: #3c3c3c;
  font-weight: 300;
  font-size: clamp(16px, 4vw, 20px);
  line-height: 150%;
  text-align: left;
`;

const Orbit = styled.div`
  position: relative;
  border-radius: 100%;
  border: 1px solid #0a439266;
  padding: 70px 70px 70px 80px;

  &:before {
    position: absolute;
    content: "";
    top: 78%;
    left: 25%;
    width: 90px;
    height: 90px;
    background-image: url(${Star_1});
  }

  &:after {
    position: absolute;
    content: "";
    top: -50px;
    left: 50%;
    width: 100px;
    height: 100px;
    background-image: url(${Star_2});
  }
`;

const about = ({ language }) => {
  const AdsList = [
    {
      title:
        language === "Ru"
          ? "Реклама в мессенджерах"
          : EngLoc.about.adsType.messangers.title,
      desc:
        language === "Ru"
          ? "Автоматизация процессов от выбора источников, до размещений и пост-анализа"
          : EngLoc.about.adsType.messangers.desc,
      img: textAds,
      line: true,
    },
    {
      title:
        language === "Ru"
          ? "Нативная реклама в социальных сетях"
          : EngLoc.about.adsType.native.title,
      desc:
        language === "Ru"
          ? "Автоматизация процессов от выбора источников, до размещений и пост-анализа"
          : EngLoc.about.adsType.native.desc,
      img: nativeAds,
      line: true,
    },
    {
      title:
        language === "Ru"
          ? "Видео-реклама на сайтах"
          : EngLoc.about.adsType.video.title,
      desc:
        language === "Ru"
          ? "Размещение и продвижение ваших видео с охватом более 25м показов в сутки"
          : EngLoc.about.adsType.video.desc,
      img: videoAds,
      line: false,
    },
  ];

  return (
    <StyledAbout id={"about"}>
      <AboutPart>
        <HightTitle>SocialJet</HightTitle>
        <Orbit>
          <AboutText>
            {language === "Ru"
              ? `С 2015 года являемся одними из крупнейших поставщиков трафика в
            социальных сетях и мессенджерах. Предоставляем сервис, аналитику и
            поддержку`
              : EngLoc.about.orbit}
          </AboutText>
        </Orbit>
      </AboutPart>
      <AboutPart>
        {AdsList.map((ad) => (
          <AdsType
            key={Math.random()}
            adTitle={ad.title}
            description={ad.desc}
            adImg={ad.img}
            line={ad.line}
          />
        ))}
      </AboutPart>
    </StyledAbout>
  );
};

export default about;
